.tabs {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  box-shadow: 0px 10px 50px 0px rgba(46, 184, 151, 0.10);

  @include laptop {
    display: block;
  }

  &__navigation {
    display: none;

    @include laptop {
      display: flex;
    }
  }

  &__link {
    border: none;
    background-color: #E8F2FC;
    text-align: center;
    padding: 30px 40px 27px;
    cursor: pointer;
    color: #2EB897;
    font-size: 2.6rem;
    line-height: 2.99rem;

    &--active {
      background-color: #F6FAFF;
      color: #212121;
    }
  }

  &__item {
    border-radius: 10px;
    overflow: hidden;
  }

  details:not([open]) {

    @include laptop {
      display: none;
    }
  }

  details[open] summary {
    color: #212121;

    &::after {
      background: url("../img/arrow-up.svg") no-repeat center;
    }

    @include laptop {
      display: none;
    }
  }

  &__title {
    padding: 16px 20px 18px;
    color: #2EB897;
    font-size: 1.8rem;
    line-height: 2.07rem;
    font-weight: 500;
    background-color: #E8F2FC;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &::marker {
      display: none;
    }

    &::after {
      content: "";
      width: 26px;
      height: 26px;
      background: url("../img/arrow-down.svg") no-repeat center;
      flex-shrink: 0;
    }
  }

  &__content {
    padding: 21px 20px 40px;
    background-color: #F6FAFF;

    @include tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      // padding: 50px 50px 60px 40px;
      column-gap: 50px;

      grid-template-areas:
      "heading image"
      "list image"
      "button image"
      ". image";
    }

    @include desktop {
      padding: 50px 50px 60px 40px;
    }

    h3 {
      font-size: 1.6rem;
      line-height: 2rem;
      margin: 0 0 18px;
      grid-area: heading;

      @include desktop {
        font-size: 3rem;
        line-height: 3.75rem;
        margin-bottom: 22px;
      }
    }

    ul {
      @include clear-list;
      display: flex;
      flex-direction: column;
      row-gap: 13px;
      padding: 0 4px;
      margin-bottom: 24px;
      grid-area: list;
      font-size: 1.6rem;
      line-height: 1.84rem;

      @include desktop {
        font-size: 2.2rem;
        line-height: 2.53rem;
        row-gap: 20px;
        margin-bottom: 34px;
      }
    }

    li {
      display: flex;
      column-gap: 13px;
      color: #6C778E;

      &::before {
        content: "";
        width: 6px;
        height: 6px;
        border-radius: 50%;
        flex-shrink: 0;
        background: linear-gradient(91deg, #11AE88 5.69%, #2A84EE 125.81%), #D9D9D9;
        margin-top: 6px;
      }
    }

    div {
      width: 100%;
      margin-bottom: 30px;
      grid-area: image;

      // @include desktop {
      // }
    }

    img {
      display: block;
      border-radius: 10px;
      margin-bottom: 26px;
      width: 100%;
      height: auto;
      margin-bottom: 0;
    }

    a {
      grid-area: button;

      @include desktop {
        width: fit-content;
      }
    }
  }
}
