.header {

  &__container {
    @include container;

    display: flex;
    align-items: center;

    padding: 20px 0;

    @include laptop {
      justify-content: space-between;
      padding: 25px 0;
    }
  }

  &__site-navigation {
    display: none;

    @include laptop {
      display: block;
    }
  }

  &__user-navigation {
    margin-left: auto;

    @include laptop {
      margin: 0;
    }
  }

  &__toggle {
    margin-left: 22px;
    width: 30px;
    height: 25px;
    background: url("../img/burger.svg") no-repeat center;
    cursor: pointer;
    border: none;

    span {
      display: none;
    }

    @include laptop {
      display: none;
    }
  }
}
