.advantages {
  padding: 30px 0 20px;

  &__title {
    margin-bottom: 20px;

    @include desktop {
      margin-bottom: 40px;
    }
  }

  &__list {
    @include clear-list;
    display: grid;
    gap: 8px;
    margin-bottom: 60px;

    @include tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: 25px;
    }

    @include laptop-l {
      grid-template-columns: repeat(3, 1fr);
      gap: 40px;
    }

    @include desktop {
      margin-bottom: 137px;
    }
  }

  &__item {
    border-radius: 10px;
    background: linear-gradient(0deg, #EAF3FD 0%, #EAF3FD 100%), #2A84EE;
    padding: 27px 20px 37px;

    @include laptop-l {
      padding: 40px;
    }
  }

  &__subtitle {
    color: #212121;
    font-size: 1.8rem;
    line-height: 2.25rem;
    margin: 0;
    margin-bottom: 5px;
    display: flex;
    column-gap: 10px;

    @include laptop-l {
      // margin-bottom: 20px;
      column-gap: 15px;
      font-size: 2rem;
      line-height: 2.5rem;
    }

    @include desktop {
      column-gap: 20px;
      font-size: 3rem;
      line-height: 3.75rem;
    }

    &::before {
      content: "";
      width: 32px;
      height: 25px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;

      @include laptop-l {
        width: 55px;
        height: 45px;
      }
    }

    &--accuracy {
      &::before {
        background-image: url("../img/accuracy.svg");
      }
    }

    &--functional {
      &::before {
        background-image: url("../img/functional.svg");
      }
    }

    &--ease {
      &::before {
        background-image: url("../img/manual.svg");
      }
    }

    &--everyone {
      &::before {
        background-image: url("../img/experience.svg");
      }
    }

    &--confidentiality {
      &::before {
        background-image: url("../img/confidentiality.svg");
      }
    }

    &--service {
      &::before {
        background-image: url("../img/service.svg");
      }
    }
  }

  &__description {
    margin: 0;
    color: #707070;
    font-size: 1.5rem;
    line-height: 1.875rem;

    @include laptop-l {
      font-size: 1.75rem;
      line-height: 2.2rem;
    }

    @include laptop-l {
      font-size: 2rem;
      line-height: 2.5rem;
    }

    @include desktop {
      font-size: 2.2rem;
      line-height: 2.75rem;
    }
  }
}
