.picture {
  padding: 30px 0 10px;

  &__title {
    margin: 0 0 20px;
  }

  &__list {
    @include clear-list;
    margin: 0 auto;
    border: 15px solid #cecece;
    display: grid;
    grid-template-columns: 2fr 6fr 1fr;
    grid-template-rows: 3fr 3fr 1fr 1fr;
    width: 650px;
    height: 650px;
    gap: 20px;
    background-color: #040B08;

    @media (max-width: $mobile-max) {
      width: 90vw;
      height: 90vw;
    }
  }

  &__list li:nth-child(1) {
    background-color: #EAE9E7;
  }

  &__list li:nth-child(2) {
    background-color: #D52315;
    grid-column: 2 / 4;
    grid-row: 1 / 3;
  }

  &__list li:nth-child(3) {
    background-color: #EAE9E7;
  }

  &__list li:nth-child(4) {
    background-color: #025B9D;
    grid-row: 3 / 5;
  }

  &__list li:nth-child(5) {
    background-color: #EAE9E7;
    grid-row: 3 / 5;
  }

  &__list li:nth-child(6) {
    background-color: #EAE9E7;
  }

  &__list li:nth-child(7) {
    background-color: #F1DD7E;
  }
}
