@mixin container {
  max-width: calc($desktop - (160px * 2));
  margin: 0 auto;
  width: calc(100% - (20px * 2));

  @include tablet {
    width: calc(100% - (50px * 2));
  }

  @include laptop {
    width: calc(100% - (70px * 2));
  }

  @include laptop-l {
    width: calc(100% - (115px * 2));
  }

  @include desktop {
    width: calc(100% - (160px * 2));
  }
}
