.gallery {
  padding: 20px 0 10px;

  &__title {
    margin: 0 0 20px;
  }

  &__list {
    @include clear-list;
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));

    @include tablet {
      grid-template-columns: repeat(auto-fit, minmax(467px, 1fr));
    }
  }

  &__item {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 20px;
    display: grid;
    justify-items: center;
    align-items: center;
  }

  &__image {
    border-radius: 20px;
    display: block;
    width: 100%;
    height: auto;
    grid-area: center;
  }

  &__link {
    font-size: 4rem;
    font-weight: 500;
    color: $white;
    grid-area: center;
  }
}
