.user-navigation {

  &__list {
    @include clear-list;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include laptop {
      column-gap: 20px;
    }
  }

  &__link {
    color: #212121;
    display: none;
    text-decoration: none;
    font-size: 1.4rem;
    line-height: 1.75rem;

    @include laptop {
      display: block;
    }

    @include laptop-l {
      display: block;
      font-size: 1.6rem;
      line-height: 2rem;
    }

    @include desktop {
      font-size: 1.8rem;
      line-height: 2.25rem;
    }

    span {
      display: none;

      @include laptop {
        display: block;
        background-color: transparent;
      }
    }

    &--sign-in {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: 8px;

      &::before {
        content: "";
        width: 26px;
        height: 25px;
        background-image: url("../img/entrance.svg");
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    &--sign-up {
      color: $white;
    }
  }


}
