.indicators {
  @include clear-list;
  border-radius: 10px;
  background: #E8F2FC;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  row-gap: 40px;

  @include laptop {
    flex-direction: row;
  }

  @include desktop {
    column-gap: 68px;
    padding: 62px 53px 50px 43px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    row-gap: 18px;

    @include desktop {
      row-gap: 26px;
    }
  }

  &__value {
    margin: 0;
    color: #2A84EE;
    font-size: 5.6rem;
    line-height: 3rem;
    letter-spacing: 0.112rem;
    font-weight: 500;

    @include desktop {
      font-size: 7rem;
      line-height: 4rem;
    }
  }

  &__description {
    margin: 0;
    color: #6C778E;
    font-size: 1.6rem;
    line-height: 2rem;

    @include desktop {
      font-size: 2.2rem;
      line-height: 2.75rem;
    }
  }
}
