.rate-card {
  padding: 30px 20px;
  border-radius: 10px;
  background-color: #EAF3FD;
  position: relative;

  @include desktop {
    padding: 50px 40px 50px;
  }

  &--white {
    background-color: $white;
    box-shadow: 0px 0px 30px 0px rgba(46, 184, 151, 0.15);
  }

  &--blue {
    background-color: #EAF3FD;
  }

  &--green {
    background-color: rgba(17, 174, 136, 0.1);
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 30px;

    @include desktop {
      row-gap: 22px;
    }
  }

  &__inner {
    min-height: 80px;
    display: flex;
    flex-direction: column;

    @include desktop {
      min-height: 112px;
    }
  }

  &__title {
    margin: 0 0 22px;
    font-size: 2rem;
    line-height: 2.5rem;

    @include desktop {
      font-size: 2.6rem;
      line-height: 3.25rem;
      margin-bottom: 15px;
    }
  }

  &__percent {
    font-size: 1.8rem;
    line-height: 2.25rem;
    position: absolute;
    top: 0;
    right: 0;
    width: 88px;
    height: 42px;
    border-radius: 0px 10px;
    background-color: #2A84EE;
    display: none;
    justify-content: center;
    align-items: center;
    color: #ffffff;

    @include desktop {
      width: 96px;
      height: 51px;
    }
  }

  &--discount {
    .rate-card__percent {
      display: flex;
    }
  }

  &__credit {
    display: none;
    color: #6C778E;
    font-size: 1.8rem;
    line-height: 2.25rem;
    margin-bottom: 13px;

    @include desktop {
      font-size: 2.2rem;
      line-height: 2.75rem;
      margin: 0;
    }
  }

  &__price {
    display: block;
    color: #2A84EE;
    font-size: 2.6rem;
    line-height: 3.25rem;

    @include desktop {
      font-size: 3rem;
      line-height: 3.75rem;
    }
  }

  &__button-wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 10px;

    @include desktop {
      row-gap: 20px;
    }
  }

  &__button {

    &--more {
      display: flex;
      justify-content: center;
      align-items: center;
      column-gap: 10px;

      &::after {
        content: "";
        width: 15px;
        height: 15px;
        background: url("../img/arrow-right.svg") no-repeat center / cover;

        @include desktop {
          width: 22px;
          height: 22px;
        }
      }
    }
  }
}

.rate-card--credit {
  .rate-card__credit {
    display: block;
  }
}

.rate-card--special {
  background: linear-gradient(87deg, #FFF1D7 0%, #FFB57F 100%), #E7F7F3;

  @media (max-width: $tablet-max) {
    padding-bottom: 250px;
  }

  .rate-card__wrapper {
    @include laptop {
      width: 26%;
    }
  }

  .rate-card__title {

    @include laptop {
      width: 200%;
    }
  }

  .rate-card__image-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;

    @media (max-width: $tablet-max) {
      right: 50%;
      transform: translateX(50%);
    }

    @include laptop {
      right: 0;
      width: 55%;
    }
  }

  .rate-card__image {
    display: block;
    margin: 0 auto;

    @include laptop {
      width: 100%;
      height: auto;
      margin: 0;
    }
  }
}
