.site-navigation {

  &__list {
    @include clear-list;
    display: flex;
    align-items: center;
    column-gap: 10px;
  }

  &__link {
    text-decoration: none;
    font-size: 1.4rem;
    line-height: 1.75rem;
    color: #212121;
    padding: 8px 15px;

    @include laptop-l {
      font-size: 1.6rem;
      line-height: 2rem;
    }

    @include desktop {
      font-size: 1.8rem;
      line-height: 2.25rem;
    }
  }
}
