.test {
  background: linear-gradient(91deg, #11AE88 5.69%, #2A84EE 125.81%), #2EB897;
  border-radius: 10px;
  padding: 30px 20px 250px;
  position: relative;

  @include laptop {
    padding: 50px 40px 52px;
  }

  @include desktop {
    padding: 50px 40px 55px;
  }

  &__wrapper {
    @include laptop {
      width: 47%;
    }
  }

  &__heading {
    color: #ffffff;
    font-size: 2.1rem;
    line-height: 2.415rem;
    letter-spacing: 0.42px;
    margin: 0 0 13px;

    @include desktop {
      font-size: 4.6rem;
      line-height: 2.75rem;
      margin-bottom: 30px;
    }
  }

  &__appeal {
    color: $white;
    font-size: 1.6rem;
    line-height: 2rem;
    letter-spacing: 0.032rem;
    margin: 0 0 27px;

    @include desktop {
      font-size: 2.1rem;
      line-height: 2.625rem;
      margin-bottom: 40px;
    }
  }

  &__form {
    position: relative;
  }

  &__input {
    @include clear-input;
    margin-bottom: 20px;
    display: block;
    width: 100%;
    padding: 13px 22px 14px 19px;
    background-color: #ffffff;
    border-radius: 30px;

    color: #C3C3C3;
    font-size: 1.6rem;
    line-height: 2rem;
    letter-spacing: 0.032rem;

    @include desktop {
      padding: 13px 150px 14px 23px;
      font-size: 2rem;
      line-height: 2.5rem;
      margin-bottom: 0;
    }

    &::placeholder {
      color: #C3C3C3;
      font-size: 1.6rem;
      line-height: 2rem;
      letter-spacing: 0.032rem;
      text-align: center;

      @include desktop {
        font-size: 2rem;
        line-height: 2.5rem;

      }
    }
  }

  &__button {
    display: block;
    width: 100%;
    // margin-bottom: 247px;

    @include laptop {
      margin-bottom: 0;
    }

    @include desktop {
      position: absolute;
      top: 5px;
      right: 6px;
      padding: 10px 55px;
      width: 264px;
    }
  }

  &__image-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;

    @media (max-width: 1023px) {
      right: 50%;
      transform: translateX(50%);
    }

    @include laptop {
      right: 12%;
      width: 36%;
    }
  }

  &__image {
    display: block;
    margin: 0 auto;

    @include laptop {
      width: 100%;
      height: auto;
      margin: 0;
    }
  }
}

.test--half {
  padding: 30px 20px 38px;
  margin-bottom: 37px;

  @include desktop {
    padding: 50px 40px;
    margin-bottom: 100px;
  }

  .test__appeal {
    margin-bottom: 30px;
  }

  .test__wrapper {
    width: 100%;
    // @include desktop {
    // }
  }
}
