.rate {
  padding: 26px 0 30px;

  @include desktop {
    padding: 70px 0 30px;
  }

  &__title {
    margin: 0 0 23px;

    @include desktop {
      margin-bottom: 32px;
    }
  }

  &__form {
    overflow: hidden;
    display: flex;
    column-gap: 8px;
    margin-bottom: 30px;

    @include desktop {
      margin-bottom: 43px;
      column-gap: 40px;
    }
  }

  &__button-form {
    margin: 0;
    padding: 8px 28px;
    border-radius: 30px;
    border: 1px solid #DBE1E9;
    font-family: $basic-font;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 2rem;
    min-width: 132px;
    background-color: transparent;
    white-space: nowrap;
    cursor: pointer;

    @include desktop {
      min-width: 234px;
      font-size: 1.8rem;
      line-height: 2.25rem;
    }

    &--active {
      background-color: #11AE88;
      color: $white;
    }
  }

  &__list {
    @include clear-list;
    display: flex;
    flex-direction: column;
    gap: 10px;

    @include laptop {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 30px 20px;
    }

    @include laptop-l {
      gap: 70px 40px;
    }
  }

  &__item {
    &--special {
      @include laptop {
        grid-column: span 3;
      }
    }
  }
}
