.hero {
  padding: 15px 0;
  position: relative;

  @include desktop {
    padding: 53px 0 51px;
    position: relative;
  }

  &__title {
    color: #212121;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: 2.8rem;
    line-height: 3.22rem;
    letter-spacing: 0.05rem;
    margin-bottom: 15px;

    @include laptop {
      font-size: 3.5rem;
      line-height: 4.38rem;
    }

    @include laptop-l {
      width: 54%;
      font-size: 4.5rem;
      line-height: 5.63rem;
      letter-spacing: 0.12rem;
    }

    @include desktop {
      width: 68.8%;
      font-size: 6rem;
      line-height: 7.5rem;
      letter-spacing: 0.12rem;
      margin-bottom: 33px;
    }

    span {
      &:nth-child(1) {
        color: #2EB897;
      }

      &:nth-child(2) {
        color: #2A84EE;
      }
    }

    img {
      margin-bottom: -5px;

      @include laptop-l {
        margin-bottom: -18px;
      }
    }
  }

  &__appeal {
    color: #6C778E;
    font-size: 1.8rem;
    line-height: 2.25rem;
    letter-spacing: 0.036rem;
    margin: 0 0 36px;

    @include laptop {
      width: 51%;
    }

    @include desktop {
      font-size: 2.6rem;
      line-height: 3.25rem;
      letter-spacing: 0.052rem;
      margin-bottom: 55px;
    }
  }

  &__test {
    margin-bottom: 40px;

    @include laptop {
      width: 49%;
    }
  }

  &__image {
    display: none;

    @include laptop {
      position: absolute;
      display: block;
      z-index: -1;
      right: 0;
      // width: 45.3%;
      width: clamp(35%, 50%, 500px);
      height: auto;
      top: 70px;
    }

    @include laptop-l {
      width: clamp(35%, 50%, 600px) ;
      top: 60px;
    }

    @include desktop {
      top: 84px;
      width: 45.3%;
    }
  }
}
