/* FONTS */
@font-face {
  font-family: "NT Somic";
  src: url("../fonts/nt-somic-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NT Somic";
  src: url("../fonts/nt-somic-medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NT Somic";
  src: url("../fonts/nt-somic-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
/* COMMONS */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* BLOCKS */
.page {
  height: 100%;
  margin: 0;
  padding: 0;
  /* 10px */
  font-size: 62.5%;
}

.page__body {
  height: 100%;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-family: "NT Somic", "Arial", sans-serif;
  font-size: 2.2rem;
  line-height: 2.75rem;
  letter-spacing: 0.032rem;
  font-weight: 400;
  color: #212121;
}
.page__body::after {
  content: "";
  position: fixed;
  z-index: -2;
  inset: 0;
  width: 100%;
  height: 100%;
}

.button {
  padding: 13px 35px;
  border-radius: 30px;
  border: none;
  background: linear-gradient(91deg, #11AE88 5.69%, #2A84EE 169.72%);
  color: #ffffff;
  display: block;
  font-family: "NT Somic", "Arial", sans-serif;
  text-align: center;
  text-decoration: none;
  font-size: 1.6rem;
  line-height: 2rem;
  cursor: pointer;
}
@media (min-width: 1920px) {
  .button {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }
}
.button:hover, .button:focus {
  background: linear-gradient(91deg, #2A84EE 5.69%, #11AE88 169.72%), #11AE88;
  box-shadow: 0px 0px 30px 0px #50ABDC;
  transition: all 0.3s;
}
@media (max-width: 1919px) {
  .button--gradient-reverse {
    background: linear-gradient(91deg, #2A84EE 5.69%, #11AE88 169.72%), #11AE88;
    box-shadow: 0px 0px 30px 0px #50ABDC;
  }
  .button--gradient-reverse:hover, .button--gradient-reverse:focus {
    box-shadow: 0px 0px 30px 0px #ffffff;
    transition: all 0.3s;
  }
}
.button--transparent {
  background: transparent;
  border: 1px solid #11AE88;
  color: #212121;
}
.button--transparent:hover, .button--transparent:focus {
  background: #fdfcfc;
  box-shadow: 0px 0px 30px 0px #50ABDC;
  transition: all 0.3s;
}

