.reviews {
  padding: 26px 0 55px;

  @include desktop {
    padding: 50px 0 125px;
  }

  &__header {
    margin-bottom: 28px;

    @include laptop {
      display: flex;
      justify-content: space-between;
    }

    @include desktop {
      margin-bottom: 40px;
    }
  }

  &__title {
    margin: 0;

    @include desktop {
      margin-bottom: 0;
    }
  }

  &__controls-wrapper {
    display: none;

    @include laptop {
      display: flex;
      column-gap: 34px;
    }
  }

  &__control {
    width: 48px;
    height: 48px;
    cursor: pointer;
    background-color: transparent;
    border: none;

    &--prev {
      background: url("../img/arrow-left.svg") no-repeat center / cover;
    }

    &--next {
      background: url("../img/arrow-right.svg") no-repeat center / cover;
    }
  }

  &__list {
    @include clear-list;
    margin-bottom: 14px;

    @include laptop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 40px;
      margin: 0;
    }
  }

  &__item {
    display: none;
    position: relative;
    border-radius: 10px;
    background: url("../img/quote.svg") no-repeat, linear-gradient(0deg, #EAF3FD 0%, #EAF3FD 100%), #2A84EE;
    background-position: top 24px left 10px;
    padding: 37px 20px 92px;

    &--show {
      display: block;
    }

    @include desktop {
      padding: 57px 50px 140px 51px;

      background-size: 223px 144px;
      background-position: top 40px left 40px;
    }
  }

  &__item:nth-child(2) {
    @media (max-width: 1023px) {
      display: none;
    }
  }

  blockquote {
    margin: 0;
  }

  &__text {
    margin: 0;
    color: #6C778E;
    font-size: 1.5rem;
    line-height: 1.875rem;

    @include laptop {
      font-size: 2.2rem;
      line-height: 2.75rem;
    }
  }

  &__accordion {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 240px;
    left: 0;
    bottom: 0;
    border-radius: 10px;
    background: linear-gradient(180deg, rgba(234, 243, 253, 0.00) 0%, #EAF3FD 50.52%);
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    row-gap: 26px;

    @include desktop {
      height: 261px;
      padding: 51px;

    }
  }

  &__button {
    padding: 0;
    color: #2EB897;
    font-size: 1.8rem;
    line-height: 2.25rem;
    background: transparent;
    border: none;
    display: flex;
    align-items: center;
    column-gap: 10px;
    cursor: pointer;

    @include desktop {
      font-size: 2.4rem;
      line-height: 3rem;
    }

    &::after {
      content: "";
      width: 12px;
      height: 8px;
      background: url("../img/arrow-green-down.svg") no-repeat center;
    }

    &--hidden {
      display: none;
    }
  }

  &__author {
    margin: 0;
    font-size: 1.8rem;
    line-height: 2.25rem;

    @include desktop {
      font-size: 2.6rem;
      line-height: 3.25rem;
    }
  }

  &__pagination {
    display: flex;
    justify-content: center;
    column-gap: 6px;

    @include laptop {
      display: none;
    }
  }

  &__indicator {
    padding: 0;
    margin: 0;
    width: 6px;
    height: 6px;
    background-color: #EBF4FE;
    border: none;
    border-radius: 50%;

    &--active {
      background-color: #2EB897;
    }
  }
}
