.logo {
  display: flex;
  align-items: center;
  column-gap: 30px;

  &__image {
    display: block;
    height: auto;

    @include desktop {
      width: 260px;
    }
  }

  &__text {
    display: none;
    margin: 0;

    font-size: 1.4rem;
    line-height: 1.75rem;
    color: #969EB1;

    @include desktop {
      display: block;
    }
  }

  &__market {

    &--wb {
      color: #702A8A;
    }

    &--ozon {
      color: #1065FF;
    }
  }

}
