.payment {
  @include clear-list;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  row-gap: 17px;
  margin-bottom: 30px;

  @include laptop {
    margin: 0;
  }

  li:nth-child(1) {
    margin-right: 8px;

    @include laptop {
      margin-right: 16px;
    }
  }

  li:nth-child(2) {
    margin-right: 16px;

    @include laptop {
      margin-right: 25px;
    }
  }

  li:nth-child(3) {

    @include laptop {
      margin-right: 25px;
    }
  }

  li:nth-child(4) {
    margin-right: 17px;

    @include laptop {
      margin-right: 26px;
    }
  }

  &__image {
    display: block;
  }
}
