.socials {
  @include clear-list;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 11px;

  &__link {
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    width: 60px;
    height: 60px;

    @include desktop {
      width: 70px;
      height: 70px;
    }

    &--vkontakte {
      background-image: url("../img/vkontakte.svg");
    }

    &--telegram {
      background-image: url("../img/telegram.svg");
    }

    &--youtube {
      background-image: url("../img/youtube.svg");
    }
  }
}
