.reports {
  padding: 30px 0 16px;

  @include desktop {
    padding: 38px 0 38px;
  }

  &__title {
    margin-bottom: 11px;

    @include desktop {
      margin-bottom: 28px;
      width: 55%;
    }
  }

  &__subtitle {
    color: #707070;
    font-size: 1.8rem;
    line-height: 2.25rem;
    margin: 0;
    margin-bottom: 25px;

    @include desktop {
      font-size: 2.6rem;
      line-height: 3.25rem;
      margin-bottom: 32px;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;

    @include tablet {
      flex-direction: row;
      align-items: start;
      gap: 130px;
    }
  }

  &__list {
    @include clear-list;
    display: flex;
    flex-direction: column;
    gap: 9px;

    @include desktop {
      gap: 14px;
    }
  }

  &__item {
    border-radius: 10px;
    background-color: #EAF3FD;
    padding: 20px 20px 25px;
    display: flex;
    column-gap: 9px;

    @include desktop {
      padding: 30px 40px 40px;
    }

    &::before {
      content: "";
      min-width: 20px;
      height: 20px;
      background: url("../img/check.svg") no-repeat center;

      @include desktop {
        min-width: 26px;
        height: 26px;
      }
    }
  }

  &__text {
    margin: 0;
    color: #212121;
    font-size: 1.6rem;
    line-height: 2rem;

    @include desktop {
      font-size: 2.6rem;
      line-height: 3.25rem;
    }
  }

  &__image {
    display: block;
  }
}
