.footer {
  background-color: #E8F2FC;

  &__container {
    @include container;
    padding: 50px 0;

    @include laptop {
      padding: 60px 0;
      display: grid;
      grid-template-columns: 1fr 2fr;
      grid-template-areas:
        "contacts block-group"
        "copyright ."
        "disclaimer payment"
        ". design";
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    margin-bottom: 32px;

    grid-area: contacts;
  }

  &__logo {
    flex-direction: column;
    align-items: start;
    row-gap: 14px;

    p {
      display: block;
    }
  }

  &__block-group {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px 20px;
    margin-bottom: 30px;
    grid-area: block-group;

    @include mobile-only {
      div:nth-child(3) {
        grid-column: span 2;
      }
    }

    @include tablet {
      grid-template-columns: repeat(3, 1fr);
    }

    @include laptop {
      column-gap: 0;
      margin: 0;
    }
  }

  &__item,
  &__list {
    display: flex;
    flex-direction: column;
    row-gap: 26px;
  }

  &__title {
    margin: 0;
    font-size: 1.8rem;
    line-height: 2.25rem;
  }

  &__list {
    @include clear-list;

    a {
      display: block;
      text-decoration: none;
      font-size: 1.8rem;
      line-height: 2.25rem;
      color: #6C778E;
    }
  }

  &__payment {
    grid-area: payment;

    @include laptop {
      justify-self: end;
    }
  }

  &__copyright,
  &__disclaimer,
  &__design {
    margin: 0;
    color: #6C778E;
    font-size: 1.6rem;
    line-height: 2rem;
    margin-bottom: 27px;
  }

  &__copyright {
    grid-area: copyright;
  }

  &__disclaimer {
    grid-area: disclaimer;
  }

  &__design {
    grid-area: design;

    span {
      color: #2EB897;
    }

    @include laptop {
      justify-self: end;
    }
  }
}
