.charts {
  @include clear-list;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 30px;
  background-color: #d1e7ff;
  padding: 20px;

  &__item {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 20px;

    &:nth-child(1) {
      grid-column: span 6;
    }
    &:nth-child(2) {
      grid-column: span 2;
    }
    &:nth-child(3) {
      grid-column: span 2;
    }
    &:nth-child(4) {
      grid-column: span 2;
    }
    &:nth-child(5) {
      grid-column: span 3;
    }
    &:nth-child(6) {
      grid-column: span 3;
    }
  }

  &__image {
    border-radius: 20px;
    display: block;
    width: 100%;
    height: auto;
  }
}
