.page {
  height: 100%;
  margin: 0;
  padding: 0;

  /* 10px */
  font-size: 62.5%;
}

.page__body {
  height: 100%;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-family: $basic-font;
  font-size: 2.2rem;
  line-height: 2.75rem;
  letter-spacing: 0.032rem;
  font-weight: 400;
  color: #212121;

  &::after {
    content: "";
    position: fixed;
    z-index: -2;
    inset: 0;

    width: 100%;
    height: 100%;
  }
}
