.dashboard-navigation {
  background-color: #badbff;

  &__list {
    @include clear-list;
  }

  &__link {
    text-decoration: none;
    color: #ffffff;
    padding: 10px 15px;
    display: block;
    border: 1px solid #90c3fa;
    background-color: #a9d0fa;

    &:hover,
    &:focus {
      background-color: #9ecaf8;
    }

    &:active {
      background-color: #84bcf7;
    }
  }
}
