.dashboard {
  padding: 20px 0 10px;
  display: grid;
  background-color: #e8f2fc;

  @include tablet {
    grid-template-columns: 1fr 3fr;
    grid-template-areas:
      "title title"
      "navigation charts"
    ;

    &__title {
      grid-area: title;
    }

    &__navigation {
      grid-area: navigation;
    }

    &__charts {
      grid-area: charts;
    }
  }

  &__title {
    margin: 0 0 20px;
  }
}
