.button {
  padding: 13px 35px;
  border-radius: 30px;
  border: none;
  background: linear-gradient(91deg, #11AE88 5.69%, #2A84EE 169.72%);
  color: $white;
  display: block;
  font-family: $basic-font;
  text-align: center;
  text-decoration: none;
  font-size: 1.6rem;
  line-height: 2rem;
  // letter-spacing: 0.16rem;
  cursor: pointer;

  @include desktop {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }

  &:hover,
  &:focus {
    background: linear-gradient(91deg, #2A84EE 5.69%, #11AE88 169.72%), #11AE88;
    box-shadow: 0px 0px 30px 0px #50ABDC;
    transition: all 0.3s;
  }

  &--gradient-reverse {
    @media (max-width: $laptop-l-max) {
      background: linear-gradient(91deg, #2A84EE 5.69%, #11AE88 169.72%), #11AE88;
      box-shadow: 0px 0px 30px 0px #50ABDC;

      &:hover,
      &:focus {
        box-shadow: 0px 0px 30px 0px #ffffff;
        transition: all 0.3s;
      }
    }
  }

  &--transparent {
    background: transparent;
    border: 1px solid #11AE88;
    color: #212121;

    &:hover,
    &:focus {
      background: #fdfcfc;
      box-shadow: 0px 0px 30px 0px #50ABDC;
      transition: all 0.3s;
    }
  }
}
